html {
  font-family: Roboto;
  font-weight: 400;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto;
  font-weight: 400;
}

.card__image {
  margin-inline: 3px;
  background-image: url("imac.5fd4cbd7.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__text {
  margin: 0;
  padding: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text--product-code {
  color: #616070;
  margin-bottom: 16px;
}

.card__text--reviews {
  color: #060b35;
  align-self: end;
}

.card__row {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__row--feedback {
  margin-bottom: 24px;
}

.card__row--price {
  margin-bottom: 16px;
}

.card__text--price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__text--costs {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__link:hover {
  background-color: inherit;
  color: #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
